<template>
  <div class="document">
    <div :class="paragraph.class" v-for="(paragraph, h) in document.paragraphs" :key="getKey(h)">
      <span class="sentence" v-for="(sentence,i) in paragraph.sentences" :key="getKey(h,i)">
        <Token v-for="(token, j) in sentence.tokens" :tokenId="getKey(h,i,j)"
              :token=token
              :key="getKey(h,i,j)"
              :isSelected="selectedTokenId==getKey(h,i,j)"
              :hasInterlinear="hasInterlinear"/>
      </span>
    </div>
  </div>
</template>

<script>
import Token from './Token.vue';
import { bus } from '../main';
export default {
  name: 'Document',
  props:['document', 'hasInterlinear'],
  data:function(){
    return {
      selectedTokenId:null
    }
  },
  components: {
    Token
  },
  methods:{
    getKey:function(h,i,j){
      let key = 'h' + h;
      if(!isNaN(i)){
        key += 'i' + i;
        if(!isNaN(j)){
          key += 'j' + j;
        }
      }
      return key;
    }
  },
  mounted(){

/* 
    TODO: 
      Does alternative stem form block derived forms?
      Handle özeng, özem etc.
      qaranga! be/qinga! etc.
      arabic reversal character
*/
    bus.$on('update-selected', tokenId => {
        this.selectedTokenId = tokenId;
    })
  }
}
</script>
